.mm-reviews {
  margin-bottom: 30px; }
  .mm-reviews_container {
    display: flex;
    padding: 0 10px;
    flex-direction: column; }
    @media screen and (min-width: 980px) {
      .mm-reviews_container {
        max-width: 980px;
        margin: 0 auto;
        padding: 0 20px; } }

.mm-review {
  display: flex;
  justify-content: space-between;
  margin-bottom: 18px; }
  .mm-review_body {
    width: 70%;
    border-bottom: 1px solid #c3e7e5; }
    .mm-review_body h3 {
      margin-bottom: 0;
      margin-top: 10px;
      font-weight: 400; }
    .mm-review_body p {
      margin-top: 0;
      font-style: italic;
      margin-bottom: 10px; }
  .mm-review_date {
    color: #38afa9; }
  .mm-review_title {
    color: #949393;
    margin-bottom: 10px; }
  .mm-review_addon {
    position: relative;
    width: 28%; }
  .mm-review_btn {
    margin-top: auto;
    position: absolute;
    bottom: 0;
    right: 0;
    background-color: #38afa9;
    color: #fff;
    border: 0;
    height: 40px;
    width: 100%;
    cursor: pointer; }
  @media screen and (max-width: 768px) {
    .mm-review {
      flex-direction: column; }
      .mm-review_body {
        width: 100%; }
      .mm-review_addon {
        width: 100%;
        position: static; }
      .mm-review_btn {
        position: static; } }
